$size: 1em;

.plain-loading {
  display: inline-block;
  vertical-align: -0.15em;

  /*---------------------------------------svg-------------------------------------------*/
  &.plain-loading-ice {
    stroke: currentColor;
    stroke-width: 4;

    & > svg {
      width: $size;
      height: $size;
      display: block;
    }

    svg:not(.plain-loading-ice-inactive) {
      @keyframes ice-svg-item {
        0% {
          opacity: 1;
        }
        @for $i from 2 through 5 {
          #{($i - 1) / 5 * 100}% {
            opacity: 1 - ($i - 1) / 5;
          }
        }
        100% {
          opacity: 0;
        }
      }
      use {
        opacity: 0;
        @for $i from 1 through 6 {
          &:nth-child(#{$i}) {
            animation: ice-svg-item 1s linear infinite #{ ($i - 1) / 6}s;
          }
        }
      }
    }

  }

  /*---------------------------------------alpha-------------------------------------------*/
  &.plain-loading-alpha {
    height: $size;
    width: $size;

    .plain-loading-tag {
      $centerRadius: (0.5*$size)/1.2;
      $itemWidth: (0.35*$size)/1.2;
      $itemHeight: (0.1*$size)/1.2;
      $width: $centerRadius + $itemWidth * 2;
      $height: $width;

      width: $width;
      height: $height;
      position: relative;
      display: block;

      .plain-loading-inner {
        background-color: currentColor;
        display: inline-block;
        width: $itemWidth;
        height: $itemHeight;
        margin-top: $itemHeight / 2 * -1;
        margin-left: $centerRadius / 2;
        top: 50%;
        left: 50%;
        position: absolute;
        transform-origin: ($centerRadius / 2 * -1) ($itemHeight / 2);
        border-radius: 1px;
        @for $i from 1 through 12 {
          &:nth-child(#{$i}) {
            transform: rotate(($i - 1) * 30deg);
            animation: plain-loading-tag 1s linear infinite #{-1 + $i / 12}s;
          }
        }
        @keyframes plain-loading-tag {
          0% {
            opacity: 1;
          }
          @for $i from 1 through 11 {
            #{$i / 12 * 100}% {
              opacity: 1 - $i / 12;
            }
          }
          100% {
            opacity: 1;
          }
        }
      }
    }
  }

  /*---------------------------------------beta-------------------------------------------*/
  $tagSize: $size/5;
  $tagNum: 10;

  &.plain-loading-beta {
    height: $size;
    width: $size;
    position: relative;

    .plain-loading-tag {
      position: absolute;
      height: 50%;
      width: $tagSize;
      left: ($size - $tagSize)/2;
      transform-origin: bottom;
      display: inline-flex;
      flex-direction: column;
      justify-content: flex-start;

      .plain-loading-inner {
        display: inline-block;
        height: $tagSize;
        width: $tagSize;
        border-radius: $tagSize;
        background-color: currentColor;
      }

      @for $i from 1 through $tagNum {
        &.plain-loading-tag-#{$i} {
          transform: rotate(($i - 1)*(360 / $tagNum)*1deg);
          animation: plain-loading-beta 1s linear infinite #{-1 + $i / $tagNum}s;
        }
      }

      @keyframes plain-loading-beta {
        0% {
          opacity: 1;
        }
        @for $i from 1 through 11 {
          #{$i / $tagNum * 100}% {
            opacity: 1 - $i / $tagNum;
          }
        }
        100% {
          opacity: 1;
        }
      }

      @for $i from 1 through $tagNum {
        .plain-loading-inner-#{$i} {
          animation: plain-loading-beta-inner 1s linear infinite #{-1 + $i / $tagNum}s;
        }
      }

      @keyframes plain-loading-beta-inner {
        0% {
          transform: scale(1);
        }
        @for $i from 1 through 11 {
          #{$i / $tagNum * 100}% {
            transform: scale((1 - $i / $tagNum)*0.75 +0.25);
          }
        }
        100% {
          transform: scale(1);
        }
      }
    }

  }

  /*---------------------------------------delta-------------------------------------------*/
  $tagSize: $size/5;
  $tagNum: 5;

  &.plain-loading-gamma {
    height: $size;
    width: $size;
    position: relative;
    animation: plain-loading-gamma 5s linear infinite;

    @keyframes plain-loading-gamma {
      from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(360deg);
      }
    }

    .plain-loading-tag {
      position: absolute;
      height: 50%;
      width: $tagSize;
      left: ($size - $tagSize)/2;
      transform-origin: bottom;
      display: inline-flex;
      flex-direction: column;
      justify-content: flex-start;

      .plain-loading-inner {
        background-color: currentColor;
        display: inline-block;
        height: $tagSize;
        width: $tagSize;
        border-radius: $tagSize;
      }

      @for $i from 1 through $tagNum {
        &.plain-loading-tag-#{$i} {
          animation: plain-loading-gamma-tag 2s cubic-bezier(.31, .64, .47, .81) infinite #{$i*0.1}s;
        }
      }

      @keyframes plain-loading-gamma-tag {
        0% {
          transform: rotate(0deg);
        }
        85% {
          transform: rotate(360deg);
        }
        100% {
          transform: rotate(360deg);
        }
      }

      @for $i from 1 through $tagNum {
        .plain-loading-inner-#{$i} {
          transform: scale((1 - $i / $tagNum)*0.5+0.5);
        }
      }
    }
  }
}
